@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-relate {
		padding: 40px 0 0;
		&__over {
			overflow-x: scroll;
		}
		&__tags {
			display: flex;
			width: fit-content;
			white-space: nowrap;
			padding: 20px 0 0;
		}
	}
}
@include sp {
	.p-relate {
		padding: pv(50) 0 0;
		&__over {
			overflow-x: scroll;
		}
		&__tags {
			display: flex;
			width: fit-content;
			white-space: nowrap;
			padding: pv(20) 0 0;
		}
	}
}
