@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-pagination {
		margin: 30px 0 0;
		text-align: center;
		&__inner {
			display: flex;
			width: fit-content;
			margin: 0 auto;
			border: 1px solid $themeColor;
		}
		span {
			display: inline-block;
			width: fit-content;
			padding: 10px;
			background: $white;
			font-size: 16px;
			border: 1px solid $themeColor;
			line-height: 1;
		}
		span.current {
			background: $themeColor;
			color: $white;
		}
		a {
			display: inline-block;
			width: fit-content;
			padding: 10px;
			background: $white;
			font-size: 16px;
			border: 1px solid $themeColor;
			line-height: 1;
			&:hover {
				@include hoverOpacity;
			}
		}
	}
}
@include sp {
	.p-pagination {
		margin: pv(30) 0 0;
		text-align: center;
		&__inner {
			display: flex;
			width: fit-content;
			margin: 0 auto;
			border: 1px solid $themeColor;
		}
		span {
			display: inline-block;
			width: fit-content;
			padding: pv(30);
			background: $white;
			font-size: pv(30);
			border: 1px solid $themeColor;
			line-height: 1;
		}
		span.current {
			background: $themeColor;
			color: $white;
		}
		a {
			display: inline-block;
			width: fit-content;
			padding: pv(30);
			background: $white;
			font-size: pv(30);
			border: 1px solid $themeColor;
			line-height: 1;
			&:hover {
				@include hoverOpacity;
			}
		}
	}
}
