@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-breadcrumb {
		padding: 30px 0 0;
		&__inner {
			@include inner('pc');
			overflow-x: scroll;
			overflow-y: hidden;
		}
		&__list {
			display: flex;
			width: fit-content;
			white-space: nowrap;
		}
		&__item {
			line-height: 1;
			&:not(:first-of-type) {
				position: relative;
				margin: 0 0 0 2em;
				&:before {
					position: absolute;
					top: 55%;
					left: -.3em;
					transform: translate(-100%, -50%);
					font-family: "dashicons";
					content: "\f345";
				}
			}
		}
		&__link {
			display: inline-block;
			line-height: 1;
			border-bottom: 1px solid;
		}
	}
}
@include sp {
	.p-breadcrumb {
		padding: pv(30) 0;
		&__inner {
			@include inner('sp');
			overflow-x: scroll;
			overflow-y: hidden;
		}
		&__list {
			display: flex;
			width: fit-content;
			white-space: nowrap;
		}
		&__item {
			line-height: 1;
			&:not(:first-of-type) {
				position: relative;
				margin: 0 0 0 2em;
				font-size: pv(30);
				&:before {
					position: absolute;
					top: 55%;
					left: -.3em;
					transform: translate(-100%, -50%);
					font-family: "dashicons";
					content: "\f345";
				}
			}
		}
		&__link {
			display: inline-block;
			line-height: 1;
			font-size: pv(30);
			border-bottom: 1px solid;
		}
	}
}
