@use '../mixin/import' as *;
@use '../setting/import' as *;
@include pc {
	.l-header {
		background: $moderateColor;
		color: #fff;
		&__inner {
			@include inner('pc');
			display: flex;
			justify-content: space-between;
		}
	}
	.l-header-note {
		text-align: center;
		padding: 10px;
		background-color: #ddd;
		font-weight: 500;
		color: #333;
	}
}
@include sp {
	.l-header {
		width: 100%;
		background: $moderateColor;
		color: #fff;
		z-index: 200;
		&__logo {
			padding: pv(30);
			text-align: center;
		}
	}
	.l-header-note {
		text-align: center;
		padding: pv(10) 0;
		background-color: #ddd;
		color: #333;
		font-size: pv(30);
		font-weight: 500;
	}
}
