@use '../setting/import' as *;
body {
	background: $bgColor;
    color: #333333;
	font-family: 'Noto Sans JP', sans-serif;
    font-size: 16px;
    font-weight: 400;
    overflow-x: hidden;
	word-break: break-all;
	letter-spacing: .02em;
	line-height: 1;
}
img {
    display: block;
    max-width :100%;
	width: 100%;
	object-fit: cover;
}

