@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include pc {
	.c-top-btn {
		position: absolute;
		bottom: 0;
		right: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60px;
		height: 60px;
		background: $moderateColor;
	}
	.c-top-btn {
		.c-top-btn {
			&__arrow {
				color: $white;
				font-size: pv(20);
				&::before {
					@include absoluteCenter;
				}
			}
		}
	}
}
@include sp {
	.c-top-btn {
		position: absolute;
		top: 0;
		right: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: pv(100);
		height: pv(100);
		background: $moderateColor;
	}
	.c-top-btn {
		.c-top-btn {
			&__arrow {
				color: $white;
				font-size: pv(40);
				&::before {
					@include absoluteCenter;
				}
			}
		}
	}
}
