@use '../mixin/import' as *;
@use '../setting/import' as *;
@include pc {
	.l-post {
		&__inner {
			@include mInner('pc');
		}
		&__list {
			margin: 30px 0 0;
			&--suggest {
				margin: 30px 0 50px;
			}
		}
		&__item {
			background: $white;
			border: 2px solid $themeColor;
			&:not(:first-of-type) {
				margin-top: 30px;
			}
		}
		&__desc {
			position: relative;
			display: flex;
		}
		&__thumb {
			width: 40%;
			aspect-ratio: 3/2;
			img {
				width: 100%;
				// height: auto;
				height: 100%;
			}
		}
		&__block {
			width: 70%;
		}
		&__ttl {
			padding: 20px 20px 40px 20px;
			font-size: 20px;
			line-height: 1.4;
		}
		&__date {
			position: absolute;
			bottom: 15px;
			right: 20px;
			font-size: 14px;
			color: $darkGray;
		}
		&__tags {
			display: flex;
			white-space: nowrap;
			width: fit-content;
		}
		&__foot {
			overflow: scroll;
			background: $themeColor;
			padding: 0 10px 10px;
		}
		&__foot + .l-post__foot {
			margin: -.5em 0 0;
		}
		&__tag {
			margin: 1em 1em 0 0;
		}
		&__err {
			padding: 50px 0 0;
		}
	}
}
@include sp {
	.l-post {
		position: relative;
		padding: pv3(50,0,70);
		&__inner {
			@include inner;
		}
		&__list {
			margin: pv(30) 0 0;
			&--suggest {
				margin: pv(30) 0 pv(60);
			}
		}
		&__item {
			background: $white;
			border: pv(3) solid $themeColor;
			&:not(:first-of-type) {
				margin-top: pv(60);
			}
		}
		&__desc {
			position: relative;
			display: flex;
			min-height: pv(260);
		}
		&__block {
			position: relative;
			width: 60%;
		}
		&__thumb {
			width: 40%;
			aspect-ratio: 3/2;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&__ttl {
			padding: pv3(20, 20, 60);
			font-size: pv(32);
			line-height: 1.4;
		}
		&__date {
			position: absolute;
			bottom: pv(15);
			right: pv(20);
			font-size: pv(28);
			color: $darkGray;
		}
		&__foot {
			padding: pv4(0, 0, 20, 20);
			overflow: scroll;
			background: $themeColor;
		}
		&__tags {
			display: flex;
			font-size: pv(30);
			white-space: nowrap;
			width: fit-content;
			margin: 0 1em 0 0;
		}
		&__tag {
			margin: 1em 1em 0 0;
		}
		&__err {
			padding: pv(50) 0 0;
			font-size: pv(30);
		}
	}
}

