@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include pc {
	.c-tag {
		display: inline-block;
		padding: 10px;
		background: $white;
		color: $themeColor;
		border-radius: 2em 0 0 2em;
		font-weight: 700;
		&:hover {
			@include hoverOpacity;
		}
		&:before {
			content: '●';
			color: $themeColor;
		}
		&--navy {
			background: $themeColor;
			color: $white;
			&::before {
				padding: 0 .5em 0 0;
				color: $white;
			}
		}
		&--orange {
			background: $moderateColor;
			color: $white;
			&::before {
				padding: 0 .5em 0 0;
				color: $white;
			}
		}
	}
}
@include sp {
	.c-tag {
		display: inline-block;
		padding: pv(15);
		background: $white;
		color: $themeColor;
		border-radius: 2em 0 0 2em;
		font-weight: 700;
		&:before {
			content: '●';
			color: $themeColor;
		}
		&--navy {
			background: $themeColor;
			color: $white;
			&::before {
				padding: 0 .5em 0 0;
				color: $white;
			}
		}
		&--orange {
			background: $moderateColor;
			color: $white;
			&::before {
				padding: 0 .5em 0 0;
				color: $white;
			}
		}
	}
}
