@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include pc {
	.c-headline {
		display: flex;
		align-items: center;
		&--border {
			position: relative;
			background: $bgColor;
			z-index: 1;
			&::before {
				position: absolute;
				top: 50%;
				right: 0;
				width: 100%;
				height: 3px;
				border-radius: 10px;
				transform: translate(0, -50%);
				background: $gray;
				z-index: -1;
				content: '';
			}
		}
		&__txt {
			font-size: 25px;
			font-weight: 700;
			padding: 0 10px;
			&--border {
				background: $bgColor;
			}
		}
		&__icon {
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			width: 30px;
			margin: 5px 0 0 0;
			color: $accentColor;
			&--border {
				background: $bgColor;
			}
			.c-headline__insect {
				font-size: 30px;
				&::before {
					@include absoluteCenter;
				}
				&--orange {
					color: $moderateColor;
				}
			}
		}
	}
}
@include sp {
	.c-headline {
		display: flex;
		align-items: center;
		&--center {
			justify-content: center;
		}
		&--border {
			position: relative;
			background: $bgColor;
			z-index: 1;
			&::before {
				position: absolute;
				top: 50%;
				right: 0;
				width: 100%;
				height: pv(3);
				border-radius: pv(10);
				transform: translate(0, -50%);
				background: $gray;
				z-index: -1;
				content: '';
			}
		}
		&__txt {
			font-size: pv(50);
			font-weight: 700;
			padding: 0 pv(10);
			&--border {
				background: $bgColor;
			}
		}
		&__icon {
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			width: pv(50);
			margin: pv4(5, 0, 0, 0);
			color: $accentColor;
			&--border {
				background: $bgColor;
			}
			.c-headline__insect {
				font-size: pv(50);
				&::before {
					@include absoluteCenter;
				}
			}
			// margin-right: pv(20);
		}
	}
}
