@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-acc {
		margin: 30px 0 0;
		background: $white;
		border: 2px solid $gray;
		outline: none;
		&__ttl {
			padding: 15px 0;
			text-align: center;
			cursor: pointer;
		}
		&__indexes {
			padding: 20px 40px;
			border-top: 2px dashed $gray;
		}
		&__index {
			&:not(:first-of-type) {
				margin: 20px 0 0;
			}
		}
		&__anchor {
			position: relative;
			display: inline-block;
			line-height: 1.4;
			text-decoration: underline;
			&::before {
				position: absolute;
				top: 50%;
				left: -.5em;
				width: 8px;
				height: 8px;
				border-radius: 50%;
				background: $darkGray;
				transform: translate(-100%, -50%);
				content: '';
			}
		}
	}
}
@include sp {
	.p-acc {
		margin: pv(50) 0 0;
		background: $white;
		border: pv(3) solid $gray;
		outline: none;
		font-size: pv(30);
		&__ttl {
			padding: pv(20) 0;
			text-align: center;
			cursor: pointer;
		}
		&__indexes {
			padding: pv2(40, 60);
			border-top: pv(3) dashed $gray;
		}
		&__index {
			&:not(:first-of-type) {
				margin: pv(30) 0 0;
			}
		}
		&__anchor {
			position: relative;
			display: inline-block;
			line-height: 1.4;
			text-decoration: underline;
			&::before {
				position: absolute;
				top: 50%;
				left: -.5em;
				width: pv(10);
				height: pv(10);
				border-radius: 50%;
				background: $darkGray;
				transform: translate(-100%, -50%);
				content: '';
			}
		}
	}
}
