@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include sp {
	.c-burger {
		position: relative;
		transition: all .3s;
		z-index: 100;
		width: pv(50);
		height: pv(44);
		margin: 0 auto;
		z-index: 200;
		&__bar {
			position: absolute;
			display: inline-block;
			transition: all .4s;
			left: 0;
			width: 100%;
			height: pv(4);
			border-radius: pv(4);
			background: $white;
			&:nth-of-type(1) {
				top: 0;
			}
			&:nth-of-type(2) {
				top: pv(20);
			}
			&:nth-of-type(3) {
				bottom: 0;
			}
		}
	}
	.is-open {
		.c-burger__bar {
			&:nth-of-type(1) {
				transform: translateY(pv(20)) rotate(-315deg);
			}
			&:nth-of-type(2) {
				opacity: 0;
			}
			&:nth-of-type(3) {
				transform: translateY(pv(20)) rotate(315deg);
				top: 0;
			}
		}
	}
}
