@use 'mixin/import' as *;
@use '../setting/_import' as *;
@include pc {
	.l-gnav-sp {
		display: none;
	}
}
@include sp {
	.l-gnav-sp {
		position: fixed;
		overflow-x: scroll;
		right: 0;
		bottom: 0;
		width: 100%;
		z-index: 200;
		&__list {
			display: flex;
			width: fit-content;
		}
		&__item {
			position: relative;
			width: pv(275);
			background: $themeColor;
			color: $white;
			z-index: 300;
			&:not(:last-of-type) {
				border-right: 1px solid $white;
			}
			&--menu {
				display: flex;
				align-items: center;
				width: 18vw;
				height: 18vw;
				background: $moderateColor;
			}
		}
		&__link {
			display: block;
			padding: pv3(90, 0, 20);
		}
		&__txt {
			font-size: pv(28);
			text-align: center;
		}
		.l-gnav-sp__icon {
			position: absolute;
			left: 50%;
			top: 35%;
			transform: translate(-50%, -50%);
			width: pv(50);
			font-size: pv(50);
			&::before {
				@include absoluteCenter;
			}
		}
	}
}
