@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include pc {
	.c-headline1 {
		position: relative;
		background: $moderateColor;
		margin: 10px 0 0;
		padding: 20px;
		box-shadow: 0 0 0 10px $moderateColor;
		border: dashed 3px white;
		font-size: 25px;
		line-height: 1.4;
		&:before {
			position: absolute;
			left: 0;
			top: 0;
			transform: translate(-50%, -50%);
			border-width: 0 0 28px 28px;
			border-style: solid;
			border-color: $bgColor $bgColor $lightPink;
			content: '';
		}
	}
}
@include sp {
	.c-headline1 {
		position: relative;
		background: $moderateColor;
		margin: pv(10) 0 0;
		padding: pv(20);
		box-shadow: 0 0 0 pv(10) $moderateColor;
		border: dashed pv(3) white;
		font-size: pv(35);
		line-height: 1.4;
		&:before {
			position: absolute;
			left: 0;
			top: 0;
			transform: translate(-50%, -50%);
			border-width: 0 0 pv(30) pv(30);
			border-style: solid;
			border-color: $bgColor $bgColor $lightPink;
			content: '';
		}
	}
}
