@use '../mixin/import' as *;
@use '../setting/import' as *;
@include pc {
	.l-side {
		@include sInner('pc');
		height: fit-content;
		background: $themeColor;
		color: #fff;
		border: 2px solid $gray;
		&__inner {
			padding: 40px 20px;
		}
		&__block {
			&:not(:first-of-type) {
				margin: 40px 0 0;
			}
		}
		&__list {
			padding: 20px 0 0 40px;
		}
		&__item {
			&:not(:first-of-type) {
				margin: 20px 0 0;
			}
		}
		&__link {
			text-decoration: underline;
			font-size: 18px;
		}
		&__btn {
			margin: 40px 0 0;
		}
	}
}
@include sp {
	.l-side {
		position: fixed;
		top: 0;
		right: 0;
		height: 100vh;
		width: 100%;
		background: $bgColor;
		z-index: 100;
		overflow-y: scroll;
		&__inner {
			@include inner;
			width: fit-content;
			padding: pv3(224, 0, 350);
		}
		&__list {
			display: flex;
			flex-wrap: wrap;
		}
		&__item {
			width: 50%;
			font-size: pv(35);
			padding: 1em 0 0 1em;
		}
		&__link {
			text-decoration: underline;
		}
		&__block {
			&:not(:first-of-type) {
				margin-top: pv(80);
			}
		}
		&__btn {
			margin: 0 auto;
			padding-top: pv(50);
		}
	}
}
