@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-serach {
		width: 350px;
		display: flex;
		color: $white;
		&__input {
			width: 80%;
			padding: 10px;
			background: $white;
			color: $txtColor;
		}
		&__btn {
			display: inline-block;
			width: 25%;
			background: $txtColor;
			text-align: center;
			.p-serach__glass {
				position: relative;
				font-size: 30px;
				&::before {
					@include absoluteCenter;
				}
			}
			&:hover {
				background: $themeColor;
				transition: .3s;
				cursor: pointer;
			}
		}
	}
}
@include sp {
	.p-serach {
		display: flex;
		color: $white;
		&__input {
			width: 85%;
			padding: pv(20);
			border-bottom: 2px solid #333;
			font-size: pv(30);
			background: $white;
			color: $txtColor;
		}
		&__btn {
			display: inline-block;
			width: 15%;
			background: $txtColor;
			border-bottom: 2px solid #333;
			text-align: center;
			.p-serach__glass {
				position: relative;
				font-size: pv(50);
				&::before {
					@include absoluteCenter;
				}
			}
		}
	}
}
