@use '../setting/_import' as *;
@mixin inner($type:$initType){
    @if( $type == 'pc' ){
        width: 1100px;
        margin: 0 auto;
    }
    @else if( $type == 'sp' ){
        margin: pv2(0,30);
    }
}
@mixin mInner($type:$initType){
    @if( $type == 'pc' ){
        width: 700px;
        // margin: 0 auto;
    }
    @else if( $type == 'sp' ){
        margin: pv2(0,30);
    }
}
@mixin sInner($type:$initType){
    @if( $type == 'pc' ){
        width: 350px;
        // margin: 0 auto;
    }
    @else if( $type == 'sp' ){
        margin: pv2(0,30);
    }
}
