@use '../../mixin/import' as *;
@use '../../setting/import' as *;
@include pc {
	.p-flow{
		&__flow {
			display: block;
			width: 100%;
			margin: 0 0 30px;
			padding: 10px 0;
			background-color:$themeColor;
			overflow: hidden;
		}
		&__ttl {
			display: inline-block;
			color: $white;
			padding-left: 100%;
			white-space: nowrap;
			@include flowTxt;
		}
		&__ttl {
			font-size: 20px;
		}
	}
}
@include sp {
	.p-flow{
		position: relative;
		// z-index: 200;
		&__flow {
			display: block;
			width: 100%;
			padding: pv2(10, 0);
			background-color:$themeColor;
			overflow: hidden;
		}
		&__ttl {
			display: inline-block;
			color: $white;
			padding-left: 100%;
			white-space: nowrap;
			@include flowTxt;
		}
		&__ttl {
			font-size: pv(30);
		}
	}
}
