@use '../mixin/import' as *;
@use '../setting/import' as *;
@include pc {
	.l-article {
		&__inner {
			@include mInner('pc');
			padding: 0 0 40px;
		}
		&__thumb {
			@include mInner('pc');
			padding: 0 0 40px;
			aspect-ratio: 3/2;
			height: auto;
			img {
				height: 100%;
			}
		}
		&__link {
			display: block;
			position: relative;
			width: fit-content;
			margin: 20px 0 20px 1em;
			padding: 0 0 0 .5em;
			color: $linkColor;
			font-weight: bold;
			font-size: 20px;
			letter-spacing: .02em;
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				transform: translate(-100%, 0);
				font-family: "dashicons";
				content: "\f345";
			}
			&::after {
				position: absolute;
				top: 0;
				left: .5em;
				transform: translate(-100%, 0);
				font-family: "dashicons";
				content: "\f345";
			}
		}
		&__aspect {
			margin: 20px 0;
			width: 100%;
			height: auto;
			aspect-ratio: 16/9;
		}
		&__content {
			margin: 20px 0 0;
			@include c-btn('pc');
			h2 {
				position: relative;
				margin: 40px 0 30px;
				padding: 20px 10px;
				font-size: 25px;
				font-weight: bold;
				line-height: 1.6;
				&:after {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 5px;
					border-radius: 10px;
					background-image: linear-gradient(to right, $lightPink 0%, $yellow 100%);
					content: '';
				}
			}
			h3 {
				margin: 40px 0 30px;
				padding: 10px 20px;
				border-left: solid 5px $themeColor;
				font-size: 25px;
				font-weight: 700;
				line-height: 1.6;
			}
			h4 {
				position: relative;
				margin: 40px 0 30px;
				padding: 0 0 0 1em;
				font-size: 25px;
				font-weight: 700;
				line-height: 1.6;
				&::before {
					position: absolute;
					top: 50%;
					left: 0;
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background: $themeColor;
					transform: translate(0, -50%);
					content: "";
				}
			}
			h5 {
				position: relative;
				margin: 40px 0 30px;
				padding: 0 0 0 1em;
				font-size: 22px;
				font-weight: 700;
				line-height: 1.6;
				&::before {
					position: absolute;
					top: 50%;
					left: 0;
					width: 10px;
					height: 3px;
					background: $themeColor;
					transform: translate(0, -50%);
					content: "";
				}
			}
			p {
				line-height: 1.6;
				margin: 20px 0;
			}
			ul,ol {
				margin: 20px 0;
				background: $lightBlue;
				padding: 20px 30px;
				border: dashed $themeColor 2px;
			}
			li {
				position: relative;
				margin: 0 0 0 20px;
				line-height: 2;
				&::before {
					position: absolute;
					top: 0.85em;
					left: -.5em;
					width: 8px;
					height: 8px;
					border-radius: 50%;
					background: $themeColor;
					transform: translate(-100%, 0);
					content: '';
				}
			}
			a {
				font-weight: 700;
				color: $linkColor;
				line-height: 1.6;
				text-decoration: underline;
			}
			figure.wp-block-image {
				aspect-ratio: 3/2;
			}
			figure {
				margin: 20px 0;
				width: 100%;
				height: auto;
				img {
					object-fit: cover;
					width: 100%;
					height: 100%;
				}
				figcaption {
					color: $darkGray;
					font-size: 14px;
					line-height: 1.6;
					text-align: center;
				}
			}
			div {
				line-height: 1.6;
			}
			table {
				width: 100%;
				margin: 20px 0;
				background: $white;
			}
			tr {
				border: 1px solid $themeColor;
			}
			th {
				vertical-align: middle;
				padding: 10px;
				border: 1px solid $themeColor;
				line-height: 1.6;
			}
			td {
				vertical-align: middle;
				padding: 10px;
				border: 1px solid $themeColor;
				line-height: 1.6;
			}
			blockquote {
				position: relative;
				margin: 30px 0;
				padding: 20px;
				background: $white;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					color: $darkGray;
					font-size: 50px;
					font-family: "dashicons";
					transform: translate(-25%, -25%);
					content: "\f122";
				}
				&::after {
					position: absolute;
					bottom: 0;
					right: 0;
					color: $darkGray;
					font-size: 50px;
					font-family: "dashicons";
					transform: translate(25%, 25%) rotate(-180deg);
					content: "\f122";
				}
			}
			cite {
				margin: 10px 0;
				color: $darkGray;
				line-height: 1.4;
			}
			iframe  {
				width: 100%;
				height: auto;
			}
		}
	}
}
@include sp {
	.l-article {
		&__inner {
			@include inner;
			padding: pv3(40,0,80);
		}
		&__thumb {
			aspect-ratio: 3/2;
