					left: 0;
					width: pv(20);
					height: pv(5);
					background: $themeColor;
					transform: translate(0, -50%);
					content: "";
				}
			}
			p {
				font-size: pv(30);
				line-height: 1.6;
				margin: pv(50) 0;
			}
			ul,ol {
				margin: pv(50) 0;
				background: $lightBlue;
				padding: pv(20) pv(30);
				border: dashed $themeColor 2px;
			}
			li {
				position: relative;
