			figure.wp-block-image {
				aspect-ratio: 3/2;
			}
			figure.wp-block-embed {
				margin: 0;
			}
			figure {
				width: 100%;
				height: auto;
				margin:pv(50) 0;
				img {
					object-fit: cover;
