				padding: pv(20);
				border: 1px solid $themeColor;
				line-height: 1.4;
			}
			td {
				vertical-align: middle;
				padding: pv(20);
				border: 1px solid $themeColor;
				line-height: 1.4;
			}
			blockquote {
				position: relative;
				margin: pv(50) 0;
				padding: pv(20);
				background: $white;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					color: $darkGray;
					font-size: pv(70);
					font-family: "dashicons";
					transform: translate(-25%, -25%);
					content: "\f122";
				}
				&::after {
					position: absolute;
					bottom: 0;
					right: 0;
					color: $darkGray;
					font-size: pv(70);
					font-family: "dashicons";
					transform: translate(25%, 25%) rotate(-180deg);
					content: "\f122";
				}
			}
			cite {
				margin: pv(20) 0;
				line-height: 1.4;
				color: $darkGray;
			}
			iframe  {
				width: 100%;
				height: auto;
			}
		}
	}
}
