@use '../setting/_import' as *;
@mixin c-btn($type:$initType){
    @if( $type == 'pc' ){
		.c-btn {
			position: relative;
			display: block;
			min-width: 250px;
			width: fit-content;
			margin: 0 auto;
			padding: 18px 50px;
			background: $moderateColor;
			color: $white;
			border-radius: 50px;
			border-bottom: 4px solid $darkOrange;
			text-decoration: none;
			font-size: 18px;
			line-height: 1;
			font-weight: bold;
			letter-spacing: .04em;
			text-align: center;
			&--sub {
				background: $orange;
				border-bottom: 4px solid $darkOrange;
			}
			&:hover {
				transition: .3s;
				opacity: .8;
				cursor: pointer;
			}
			&::before {
				position: absolute;
				top: 52%;
				right: 0;
				transform: translate(-100%, -50%);
				font-family: "dashicons";
				content: "\f345";
			}
		}
    }
    @else if( $type == 'sp' ){
		.c-btn {
			position: relative;
			display: block;
			min-width: pv(450);
			width: fit-content;
			margin: 0 auto;
			padding: pv2(30, 80);
			background: $moderateColor;
			color: $white;
			border-radius: pv(60);
			border-bottom: pv(7) solid $darkOrange;
			text-decoration: none;
			font-weight: 700;
			letter-spacing: .04em;
			font-size: pv(32);
			line-height: 1;
			text-align: center;
			&--sub {
				background: $orange;
				border-bottom: pv(7) solid $darkOrange;
			}
			&::before {
				position: absolute;
				top: 52%;
				right: 0;
				transform: translate(-100%, -50%);
				font-family: "dashicons";
				content: "\f345";
			}
		}
    }
}
