@use '../mixin/import' as *;
@use '../setting/import' as *;
@include pc {
	.l-footer {
		position: relative;
		padding: 30px 0 50px;
		background: $txtColor;
		color: $white;
		&__inner {
			@include inner('pc');
		}
		&__list {
			display: flex;
			flex-wrap: wrap;
			margin: 20px 0 0;
		}
		&__item {
			&:not(:last-of-type) {
				margin:0 40px 0 0;
			}
		}
		&__link {
			text-decoration: underline;
		}
		&__sns {
			display: flex;
			margin: 30px 0 0;
		}
		&__icon {
			width: 30px;
			font-size: 30px;
			&:not(:last-of-type) {
				margin: 0 15px 0 0;
			}
			.l-footer__rss {
				color: $orange;
				font-size: 30px;
			}
			.l-footer__twitter {
				color: $blue;
				font-size: 30px;
			}
		}
		&__copy {
			display: block;
			margin-top: 30px;
			text-align: center;
		}
	}
}
@include sp {
	.l-footer {
		position: relative;
		padding: pv3(40, 0, 212);
		background: $txtColor;
		color: $white;
		&__inner {
			@include inner;
		}
		&__list {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			margin: pv(30) 0 0;
		}
		&__item {
			margin-top: pv(40);
			font-size: pv(30);
		}
		&__link {
			text-decoration: underline;
		}
		&__sns {
			display: flex;
			margin: pv(50) 0 0;
		}
		&__icon {
			width: pv(50);
			font-size: pv(50);
			&:not(:last-of-type) {
				margin: 0 pv(30) 0 0;
			}
			.l-footer__rss {
				color: $orange;
				font-size: pv(50);
			}
			.l-footer__twitter {
				color: $blue;
				font-size: pv(50);
			}
		}
		&__copy {
			display: block;
			margin-top: pv(40);
			text-align: center;
			font-size: pv(28);
		}
	}
}
