@use '../../mixin/import' as *;
@use '../../setting/_import' as *;
@include pc {
	.p-tag {
		&__inner {
			@include mInner('pc');
		}
		&__txt {
			margin: 30px 0 0;
		}
		&__list {
			display: flex;
			flex-wrap: wrap;
			margin: 30px 0 0;
			padding: 0 0 1em 0;
		}
		&__item {
			margin: 1em 1em 0 0;
		}
	}
}
@include sp {
	.p-tag {
		padding: pv(70) 0;
		&__inner {
			@include inner;
		}
		&__txt {
			margin: pv(50) 0 0;
			font-size: pv(30);
		}
		&__list {
			display: flex;
			flex-wrap: wrap;
			margin: pv(50) 0 0;
		}
		&__item {
			margin: 1em 1em 0 0;
			font-size: pv(30);
		}
	}
}
